
.chatRoom {
    background-color:#f4f4fc;
    padding: 50px;
}

.chatMessageRoom {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    border: 2px solid rgba(112, 124, 151, 0.25);
    border-radius: 6px;
    margin: 0px auto;
    background-color: #e5dfdf;
    width: 90%;
    height: 68vh;
    overflow-y: auto;
}
.chatMessage {
    display: flex;
    align-items: flex-end;
}
.chatName {
    margin: 10px;
    width: max-content;
}
.userInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.userPhoto {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}
.inputText:focus {
outline: none;
}

.inputText {
    width: 80%;
    height: 40px;
    border: none;
    font-family: inherit;
    padding-left: 10px;
    font-size: 1.1rem;
    color: rgb(112, 124, 151);
}


.inputArea {
    display: flex;
    justify-content: space-between;
    width: 70%;
    margin: 40px 50px;
    padding: 5px;
    border-color: none;
    border: 4px solid rgb(0 0 0 / 25%);
    border-radius: 6px;
    background-color: white;
}

.sendText {
    border-radius: 50%;
    margin-left: 10px;
    cursor: pointer;
    border: none;
    background: linear-gradient(325.78deg, #2A8BF2 14.76%, #7CB8F7 87.3%);
}
.sendText:hover {
    box-shadow: 4px 4px 25px rgba(42, 139, 242, 0.15), 2px 2px 25px rgba(42, 139, 242, 0.05), 4px 6px 10px rgba(42, 139, 242, 0.15);
}

.sendIcon {
    width: 30px;
    height: 30px;
    transform: rotate(315deg);
}

@media screen and (max-width: 600px) {
    .chatMessageRoom {
        height: auto;
        width: 98%;
        margin: 0px;
        padding-bottom: 50px;
        border: none;
    }
    .chatRoom {
        padding: 0px;
    }
    .sendText {
        margin-left: 0px;
        margin-right: 10px;
    }
    .inputArea {
        border: none;
        bottom: 0;
        height: 39px;
        margin: auto;
        position: fixed;
        width: 100%;
    }
    .chatName {
        display: none;
    }
    .userPhoto {
        width: 35px;
        height: 35px;
        margin-bottom: 10px;
    }
   }